export default {
  name: 'CoverSFW',

  props: {
    workid: {
      type: Number,
      required: true
    },
    
    nsfw: {
      type: Boolean,
      default: true
    },

    release: {
      required: true
    }
  },

  data () {
    return {
      blurFlag: true,
    }
  },

  computed: {
    coverUrl () {
      //  LocalStorage  token
      const token = this.$q.localStorage.getItem('jwt-token') || ''
      return this.workid ? `/api/cover/${this.workid}?token=${token}` : ""
    },

    rjcode () {
      return (`000000${this.workid}`).slice(-6)
    },

    imgClass () {
      if (this.$q.platform.is.mobile) {
        // 
        return ""
      } else {
        if (!this.nsfw) {
          // PCSFW
          return ""
        } else {
          // PCNSFW
          return this.blurFlag ? "blur-image" : ""
        }
      }
    }
  },

  methods: {
    toggleBlurFlag () {
      this.blurFlag = !this.blurFlag
    }
  }
}
